import logo from './logo.svg';
import './App.css';
import {Button} from 'antd';

function App() {
    return (
        <div className="App">
            <header className="App-header">
                <img src={logo} className="App-logo" alt="logo"/>
                <div>
                    Choose your satisfied name: <div id={"name"}>...</div>
                    <Button type="primary" onClick={() => choose()}>Refresh</Button>
                    <div id={"ph"}></div>
                </div>
            </header>
        </div>
    );
}

const names = ["哈哈哈哈", "hhhh", "eeeeee"]

function choose() {
    let t = document.getElementById("name").innerText
    let ns = names.copyWithin(0, 0).filter((value) => {return value !== t})

    let index = parseInt(Math.random() * ns.length)
    document.getElementById("name").innerText = ns[index]
    document.getElementById("ph").innerText = "now, you can rename your interesting project or product"
}

export default App;
